import React, { Component } from "react";

export default class AddEmployee extends Component {
  addEmployee = e => {
    e.preventDefault();

    const employee = {
      id: this.props.lastId + 1,
      birthday: this.birthdayAdd.value,
      name: this.nameAdd.value,
      phone: this.phoneAdd.value,
      role: this.roleAdd.value,
      isArchive: this.archiveAdd.checked
    };

    const data = [...this.props.data, employee];

    this.props.update({
      data: data
    });
  };

  render() {
    return (
      <div className="container mb70">
        <div className="row">
          <div className="col-md-12">
            <h2> </h2>
            <form onSubmit={this.addEmployee}>
              <div className="form-group">
                <label htmlFor="exampleName"> </label>
                <input
                  name="name"
                  ref={input => {
                    this.nameAdd = input;
                  }}
                  type="text"
                  className="form-control"
                  id="exampleName"
                  placeholder=" "
                />
              </div>
              <div className="form-group">
                <label htmlFor="inputRole"></label>
                <select
                  id="inputRole"
                  className="form-control"
                  ref={input => {
                    this.roleAdd = input;
                  }}
                >
                  <option defaultValue disabled>
                    ...
                  </option>
                  <option value="driver"></option>
                  <option value="cook"></option>
                  <option value="waiter"></option>
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="exampleTel"></label>
                <input
                  ref={input => {
                    this.phoneAdd = input;
                  }}
                  type="tel"
                  className="form-control"
                  id="exampleTel"
                  placeholder=" "
                />
              </div>
              <div className="form-group">
                <label htmlFor="exampleBirthday"> </label>
                <input
                  ref={input => {
                    this.birthdayAdd = input;
                  }}
                  type="text"
                  className="form-control"
                  id="exampleBirthday"
                  placeholder="  "
                />
              </div>
              <div className="form-group form-check">
                <input
                  ref={input => {
                    this.archiveAdd = input;
                  }}
                  type="checkbox"
                  className="form-check-input"
                  id="exampleArchived"
                />
                <label className="form-check-label" htmlFor="exampleArchived">
                   ?
                </label>
              </div>
              <button type="submit" className="btn btn-primary">
                 
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}



